import { Card, Button } from 'react-bootstrap'

export function ProjectCard(props) {
    let cardStyle = {
        minWidth: '18rem',
        boxShadow: `0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)` 
    }
    return (
        <div>
            <Card style={cardStyle}>
                <Card.Img variant="top" src={props.cardImage} style={{border: "5px solid white"}}/>
                <Card.Body>
                    <Card.Title>{props.cardTitle}</Card.Title>
                    <Card.Text>
                        {props.cardText}
                    </Card.Text>
                    <div>
                        <a href={props.cardLink} target="_blank" rel="noreferrer"><Button variant="outline-dark">See More</Button></a>
                        {props.cardCodeLink && <a href={props.cardCodeLink} target="_blank" rel="noreferrer" style={{marginLeft: "1em"}}><Button variant="outline-dark">View Code</Button></a>}
                    </div>
                </Card.Body>
            </Card>
        </div>
    );
}


