import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faEnvelopeSquare } from '@fortawesome/free-solid-svg-icons'
import { faLinkedin, faGithubSquare } from '@fortawesome/free-brands-svg-icons'
import { Header } from '../header.js'
import { Jumbotron } from 'react-bootstrap'
import { Footer } from '../footer.js'

function Contact() {
    const heroStyle = {
        position: "relative",
        color: "white",
        backgroundImage: `url("/images/lightsInHand.jpg")`,
        backgroundPosition: "center",
        backgroundRepeat: "no-repeat",
        backgroundSize: "cover",
        height: "100vh",
        filter: "blur(2px)",
    }

    const bylineStyle = {
        position: "absolute",
        backgroundColor: "rgba(0,0,0, 0.8)",
        color: "white",
        border: "3px solid white",
        top: "50%",
        left: "50%",
        transform: "translate(-50%, -50%)",
        zIndex: "10",
        padding: "10em",
        textAlign: "center",
    }

    const iconsContainerStyle = {
        display: "flex", 
        flexDirection: "horizontal",
        justifyContent: "center",
        fontSize: "3rem"
    }

    const iconStyle = {
        color: "white",
        padding: "0.3em",
    }

    return (
        <div>
            <Header />
            <Jumbotron style={heroStyle}></Jumbotron>
            <div style={bylineStyle}>
                <h1>Contact Me</h1>
                <br />
                <p>Inspired? Stumbled here by accident? Want to know more?</p>
                <div id="icons" style={iconsContainerStyle}>
                    <a style={iconStyle} target="_blank" rel="noreferrer" href="https://github.com/atreides1"><FontAwesomeIcon icon={faGithubSquare} /></a>
                    <a style={iconStyle} target="_blank" rel="noreferrer" href="https://www.linkedin.com/in/mercy-bhakta/"><FontAwesomeIcon icon={faLinkedin} /></a>
                    <a style={iconStyle} target="_blank" rel="noreferrer" href="mailto:mercybhakta@gmail.com?Subject=Hello%20World"><FontAwesomeIcon icon={faEnvelopeSquare} /></a>
                </div>
            </div>
            {/* Footer */}
            <Footer spacing={"-2em"}/>
        </div>
    );
}

export default Contact;
