import "./styles/fonts.css";
import { Header } from './header.js'
import { Footer } from './footer.js'
import { Background } from './background.js'
import { Container, Jumbotron, CardDeck, Col } from 'react-bootstrap'
import { ProjectCard } from "./card.js"


function App() {
  const heroStyle = {
    position: "relative",
    color: "white", 
    backgroundImage: `url("/images/lightsInHand.jpg")`,
    backgroundPosition: "center", 
    backgroundRepeat: "no-repeat",
    backgroundSize: "cover",
    height: "50vh"
  }
  return (
    <div>
      {/* Background - this will be a canvas element */}
      <Background />
      {/* Header */}
      <Header />
      {/* Hero */}
      <Jumbotron fluid style={heroStyle}>
        <Container>
          <span style={{ position: "absolute", bottom: "2%" }}>
            Photo by{" "}
            <a href="https://unsplash.com/@joshboot?utm_source=unsplash&amp;utm_medium=referral&amp;utm_content=creditCopyText">
              Josh Boot
            </a>{" "}
            on{" "}
            <a href="https://unsplash.com/s/photos/light?utm_source=unsplash&amp;utm_medium=referral&amp;utm_content=creditCopyText">
              Unsplash
            </a>
          </span>
        </Container>
      </Jumbotron>
      {/* Main Content */}
      <Container>
        <CardDeck>
          <Col className="container-fluid mt-4">
            <ProjectCard
              cardTitle="Snappy Webcam App"
              cardText="A place for quick webcam pics and editing right in the browser."
              cardImage="/images/snappy.png"
              cardLink="https://atreides1.github.io/snappy-webcam-app/"
              cardCodeLink="https://github.com/atreides1/snappy-webcam-app"
            />
          </Col>
          <Col className="container-fluid mt-4">
            <ProjectCard
              cardTitle="React To Do App"
              cardText="Cleanly designed To Do App made with ReactJS"
              cardImage="/images/todoApp.png"
              cardLink="https://atreides1.github.io/Todo-React-App/"
              cardCodeLink="https://github.com/atreides1/Todo-React-App"
            />
          </Col>
          <Col className="container-fluid mt-4">
            <ProjectCard
              cardTitle="Distributed Ray Tracer"
              cardText="A distributed ray tracer implemented in Python."
              cardImage="/images/raytracing.png"
              cardLink="https://github.com/atreides1/Python-Ray-Tracer"
            />
          </Col>
          <Col className="container-fluid mt-4">
            <ProjectCard
              cardTitle="Wishmaker's Path"
              cardText="A twine experience."
              cardImage="/images/wishmakersPath.png"
              cardLink="https://atreides1.github.io/wishmakers-path/"
            />
          </Col>
          <Col className="container-fluid mt-4">
            <ProjectCard
              cardTitle="Lila"
              cardText="Working with a creative partner, we used HTML, JavaScript, and CSS to create an interactive story about navigating life with anxiety."
              cardImage="/images/lila.png"
              cardLink="https://atreides1.github.io/Lara-and-Mercy-s-Game/"
              cardCodeLink="https://github.com/atreides1/Lara-and-Mercy-s-Game"
            />
          </Col>
        </CardDeck>
      </Container>
      {/* Footer */}
      <Footer spacing={"2em"} />
    </div>
  );
}

export default App;
