import { Header } from '../header.js'
import { Footer } from '../footer.js'
import { Card, Container } from "react-bootstrap";

function About() {
    let cardStyle = { 
        width: '50vw', 
        // height: '25%', 
        margin: "auto",
        boxShadow: `0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)` 
    }
    return (
      <div>
        <Header />
        {/* About Content */}
        <Container
          fluid
          className="bg-secondary"
          style={{
            width: "100%",
            height: "100vh",
            display: "flex",
            justifyContent: "center",
            backgroundImage: `url("/images/lightsInHand.jpg")`,
            backgroundPosition: "center",
            backgroundRepeat: "no-repeat",
            backgroundSize: "cover",
          }}
        >
          <Card className="bg-light" style={cardStyle}>
            <Card.Header>Who, Me?</Card.Header>
            <Card.Body>
              <Card.Text>
                Thanks for visiting! ✨✨
                <br />
                <br />
                I'm a human being (I swear!) who enjoys creative coding, helping
                others, and problem solving. Currently, I freelance as a
                full-stack web developer using React and Node.js 🔥. When I'm
                not coding, you can find me reading anything I can get my hands
                on, writing messy prose, gaming (Fire Emblem is my weakness!),
                and making art. 🎨
              </Card.Text>
            </Card.Body>
          </Card>
        </Container>
        <Footer />
      </div>
    );
}

export default About;
