import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faHandHoldingHeart } from '@fortawesome/free-solid-svg-icons'
import { faReact, faBootstrap } from '@fortawesome/free-brands-svg-icons'
import { Navbar, Nav } from 'react-bootstrap'

const el = <FontAwesomeIcon icon={faHandHoldingHeart} />;
const react = <FontAwesomeIcon icon={faReact} />;
const bootstrap = <FontAwesomeIcon icon={faBootstrap} />;

export function Footer(props) {
    return (
        <div className="bottom" style={props.spacing ? {marginTop: props.spacing} : {}}>
            <Navbar bg="dark" variant="dark" expand="lg">
                    <Nav className="ml-auto">
                        <Navbar.Text className="center">{el} Made with {react} ReactJS and {bootstrap} Bootstrap.</Navbar.Text>
                    </Nav>
            </Navbar>
        </div>
    );
}