import { Link } from "react-router-dom";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faStar } from '@fortawesome/free-solid-svg-icons'
import { Navbar, Nav } from 'react-bootstrap'

const el = <FontAwesomeIcon icon={faStar} />;
export function Header() {
    
    const navLinkStyle = {
        color: "white"
    }
    // use hooks to change to destination
    return (
        <div>
            <Navbar collapseOnSelect bg="dark" variant="dark" expand="lg">
                <Navbar.Brand><Link to={`/`} style={{fontFamily: "Source Serif Pro", color: "white"}}>Mercy Bhakta</Link></Navbar.Brand>
                <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                <Navbar.Collapse id="responsive-navbar-nav">
                    <Nav className="ml-auto">
                        <Navbar.Text>{el}</Navbar.Text>
                        <Nav.Link><Link to={`/about`} style={navLinkStyle}>About</Link></Nav.Link>
                        <Nav.Link><Link to={`/contact`} style={navLinkStyle}>Contact</Link></Nav.Link>
                    </Nav>
                </Navbar.Collapse>
            </Navbar>
        </div>
    );
}