import { useState, useEffect } from 'react';
import ParticlesBG from 'particles-bg';
// hook for current root div size
// based on:
// https://usehooks.com/useWindowSize/
function useRootSize() {
    const [rootSize, setRootSize] = useState({
        width: undefined,
        height: undefined,
    });

    // hook for root div resizing
    useEffect(() => {
        //handler for resizing
        function handleResize() {
            let el = document.getElementById("root");
            setRootSize({
              width: el.getBoundingClientRect().width,
              height: el.getBoundingClientRect().height,
            });
        }
        // add event listener
        window.addEventListener("resize", handleResize);
        handleResize();

        //remove event listener (cleanup)
        return () => window.removeEventListener("resize", handleResize);
    }); //empty array = effect only run on mount

    return rootSize;
}

export function Background() {
  const size = useRootSize();

  const backgroundStyle = {
    position: "absolute",
    zIndex: "-1", // put it behind everything
    width: `${size.width}px`,
    height: `${size.height}px`,
    backgroundColor: "rgb(25,25,25)",
  };

  // https://stackblitz.com/edit/react-sop6sx?file=index.js
  let config = {
    num: [4, 7],
    rps: 0.1,
    radius: [5, 40],
    life: [1.5, 3],
    v: [2, 3],
    tha: [-40, 40],
    alpha: [0.6, 0],
    scale: [0.1, 0.4],
    position: "all",
    color: ["#8C602A", "#DFD8D1", "#F2AC57", "#FF8E30", "#FCCD8A"],
    // color: ["random", "#ff0000"],
    cross: "dead",
    // emitter: "follow",
    random: 15,
  };

  if (Math.random() > 0.85) {
    config = Object.assign(config, {
      onParticleUpdate: (ctx, particle) => {
        ctx.beginPath();
        ctx.rect(
          particle.p.x,
          particle.p.y,
          particle.radius * 2,
          particle.radius * 2
        );
        ctx.fillStyle = particle.color;
        ctx.fill();
        ctx.closePath();
      },
    });
  }

  return (
    <div style={backgroundStyle}>
      <ParticlesBG type="custom" config={config} bg={true} />
    </div>
  );
}

